import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/login",
    component: () => import("../components/Layout.vue"),
    children: [
      {
        path: "/users",
        name: "Users",
        component: () => import("../views/Users.vue"),
      },
      {
        path: "/new-group",
        name: "Groups",
        component: () => import("../views/NewGroup.vue"),
      },
      {
        path: "/edit-group/:group_id",
        name: "Groups",
        component: () => import("../views/EditGroup.vue"),
      },
      {
        path: "/message",
        name: "Messages",
        component: () => import("../views/Messages.vue"),
      },
      {
        path: "/groups",
        name: "Groups",
        component: () => import("../views/Groups.vue"),
      },
      {
        path: "/icons",
        name: "Icons",
        component: () => import("../views/Icons.vue"),
      },
      {
        path: "/computers",
        name: "Computers",
        component: () => import("../views/Computers.vue"),
      },
    ],
  },
  {
    path: "/add-group",
    name: "Add Group",
    component: () => import("../views/AddGroup.vue"),
  },
  {
    path: "/edit-group",
    name: "Edit Group",
    component: () => import("../views/EditGroup.vue"),
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
